.header {
	margin: pxToRem(60px) 0 pxToRem(80px);
	overflow: hidden;	

	&__nav {
		padding-top: pxToRem(40px);
		border-top: 1px solid $border-color;
	}

	&__item {
		display: inline-block;
		margin-right: pxToRem(35px);
	}

	&__link {
		font-size: pxToRem(18px);
		color: $color-grey;

		&:hover,
		&.is-active{
			color: $color-font;
		}
	}
}